import React, { useState } from "react";
import Filter, { createExtraButton } from "@kne/react-filter";
import isNumber from "lodash/isNumber";

const { City, List, Range, Text, Selector, Check } = Filter.type;

const App = () => {
  const [value, setValue] = useState({});
  return <><Filter value={value} onChange={(value) => {
    setValue(value);
    console.log(value);
  }} list={[[<City label="" name="currentCity" size={5} />], [<List label="" name="degree" size={1}>
    <List.Item value="0" label="" />
    <List.Item value="1" label="" />
    <List.Item value="2" label="" />
    <List.Item value="3" label="" />
  </List>], [<List label="" name="yixiang" size={1}
                   options={[{ label: "", value: 0 }, { label: "", value: 1 }, {
                     label: "", value: 2
                   }]} />], [<Range label="" name="age" template={(value) => {
    if (!value || value.length === 0) {
      return "";
    }
    if (!isNumber(value[0]) && isNumber(value[1])) {
      return `${value[1]}`;
    }
    if (!isNumber(value[1]) && isNumber(value[0])) {
      return `${value[0]}`;
    }
    return `${value[0]}-${value[1]}`;
  }} />, <Text label="" name="inter" />, <Selector label="" name="lang"
                                                     options={[{
                                                       label: "", value: "en"
                                                     }, {
                                                       label: "", value: "jap"
                                                     }, {
                                                       label: "", value: "fac"
                                                     }]} />], [<List size={1}
                                                                     label=""
                                                                     name="sarl"
                                                                     options={[{
                                                                       label: "10k", value: [null, 10]
                                                                     }, {
                                                                       label: "10k-20k", value: [10, 20]
                                                                     }, {
                                                                       label: "20k", value: [20, null]
                                                                     }]}
                                                                     more={createExtraButton({
                                                                       type: "range", template: (value) => {
                                                                         if (!value || value.length === 0) {
                                                                           return "";
                                                                         }
                                                                         if (!isNumber(value[0]) && isNumber(value[1])) {
                                                                           return `${value[1]}k`;
                                                                         }
                                                                         if (!isNumber(value[1]) && isNumber(value[0])) {
                                                                           return `${value[0]}k`;
                                                                         }
                                                                         return `${value[0]}k-${value[1]}k`;
                                                                       }
                                                                     })} />, <Check label="" name="out" />]]} />
    <div>{JSON.stringify(value, null, 2)}</div>
  </>;
};

export default App;